<template>
  <main>
    <HeaderComponent></HeaderComponent>
    <section class="main" :style="'min-height:calc(' + $store.state.wh / 1.5 + 'px - 80px)'">
      <h1>Busque pelos produtos com menor preço</h1>
      <input type="text" id="input" v-model="query" @keyup="search" />
      <button class="av" @click="search">Pesquisa avançada</button>

    </section>
    <section class="products">
      <div class="container" :style="'min-height:calc(' + ($store.state.wh / 2.5) + 'px - 80px)'">
        <table>
          <tr>
            <th>Gpu</th>
            <th>preço</th>
            <th>preço a prazo</th>
            <th>loja</th>
            <!-- <th>Country</th> -->
          </tr>
          <tr :key="n + i" v-for="(n, i) in results.length > 0 ? results : gpusRaw" @click="goLink(n.link)">
            <td>{{
              n.brand + ' ' + n.manufacturer +
              " " +
              n.model
            }}</td>
            <td>{{ n.price }}</td>
            <td>{{ n.pricep }}</td>
            <td><img :src="require('@/assets/img/' + n.store + '.png')" style="width:40px;height:40px" alt=""></td>
            <!-- <td>Germany</td> -->
          </tr>

        </table>
      </div>
    </section>
  </main>
</template>


<script>
import HeaderComponent from "../components/HeaderComponent.vue";
import axios from "axios";
export default {
  data() {
    return {
      gpusRaw: [],
      results: [],
      query: "",
    };
  },
  mounted() {
    const self = this;
    axios.get("https://comparador.agreablecode.com/jsons/gpu.json").then((s) => {
      self.gpusRaw = s.data.sort((a, b) => {
        return a.price - b.price;
      });
      if (self.query != '') {
        self.search()
      }
    })
  },

  methods: {
    goLink(link) {
      window.open(link, "_newtab")
    },
    search() {
      let query = this.query.trim().replace(" ti", "ti");
      if (!query) {
        this.results = [];
        return;
      }
      // `(?=.*\\bgalax\\b)(?=.*\\b4070\\b)`
      let regex = query.split(" ");
      let reg = "";
      regex.forEach((element) => {
        if (element != undefined) {
          reg += "(?=.*" + element + ")";
        }
      });
      // console.log(reg)
      let pattern = new RegExp(reg.replace(/\s+/g, "\\s+"), "gi");
      this.results = this.gpusRaw.filter((product) =>
        pattern.test(
          product.model.replaceAll(" ", "") + product.brand.replaceAll(" ", "") + product.manufacturer.replaceAll(" ", "") + product.store.replaceAll(" ", "") + product.price.replaceAll(" ", "")
        )
      );
      this.results.sort((a, b) => {
        return a.price - b.price;
      });
    },
  },
  beforeUnmount() { },
  components: {
    HeaderComponent,
  },
};
</script>

