<template>
  <header>
    <div class="container">
      <router-link :to="{ path: '/' }">
        <div class="logo">
        </div>
      </router-link>
      <nav>
        <ul>
          <li><router-link :to="{ path: '/processors' }">Cpus</router-link></li>
          <li><router-link :to="{ path: '/gpu' }">Gpus</router-link></li>
          <li><router-link :to="{ path: '/motherboards' }">placas mãe</router-link></li>
          <li><router-link :to="{ path: '/ssd' }">ssd's</router-link></li>
          <li><router-link :to="{ path: '/memory' }">memorias</router-link></li>
          <li><router-link :to="{ path: '/powersuply' }">fontes</router-link></li>
        </ul>
      </nav>
      <!-- <button class="singButton">get started</button> -->
      <button class="singButton" @click="menuOpen = !menuOpen"><i
          class="menuBar"><span></span><span></span><span></span></i></button>
    </div>
  </header>
  <aside v-if="menuOpen" :style="'height:' + ($store.state.wh - 80) + 'px'">
    <div class="container">
      <nav>
        <ul>
          <li><router-link :to="{ path: '/processors' }">Cpus</router-link></li>
          <li><router-link :to="{ path: '/gpu' }">Gpus</router-link></li>
          <li><router-link :to="{ path: '/motherboards' }">motherboards</router-link></li>
          <li><router-link :to="{ path: '/ssd' }">ssd</router-link></li>
          <li><router-link :to="{ path: '/memory' }">memory</router-link></li>
          <li><router-link :to="{ path: '/powersuply' }">powersuply</router-link></li>
        </ul>
      </nav>
      <!-- <i @click="menuOpen = false" class="menuClose"><span></span><span></span><span></span></i> -->
    </div>
  </aside>
</template>
<script>
export default {
  data() {
    return {
      menuOpen: false,
    };
  },
  mounted() {
    this.checkWindowSize(); // Atualizar isLarge na montagem do componente
    // Adicionar ouvinte de evento de redimensionamento para atualizar isLarge
    window.addEventListener("resize", this.checkWindowSize);
  },
  beforeUnmount() {
    // Remover o ouvinte de evento de redimensionamento quando o componente é destruído
    window.removeEventListener("resize", this.checkWindowSize);
  },
  methods: {
    checkWindowSize() {
      this.isLarge = window.innerWidth > 768;
    },
  },
};
</script>
<style scoped lang="scss">
header {
  background: rgba(255, 255, 255, 0.786);
  display: block;
  width: 100%;
  height: 80px;
  position: fixed;
  z-index: 997;
  top: 0;
  left:0;
  outline: 1px solid red;

  .container {
    // max-width: 1500px;
    outline: 1px solid red;
    height: 100%;
    margin: auto auto;
    display: flex;
    width: 95%;
    // align-content: center;
    // justify-content: center;
    align-items: center;
    justify-content: space-between;

    .logo {
      display: block;
      height: 40px;
      width: 100px;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      background-image: url("@/assets/logo.svg");
      // border-radius:50%
    }

    nav {
      padding: 0;

      ul {
        display: flex;
        justify-content: space-between;
        list-style: none;

        li {
          padding: 0 30px;

          &:first-child {
            padding-left: 0;
          }

          &:last-child {
            padding-right: 0;
          }

          a {
            text-decoration: none;
            font-weight: 500;
            font-family: "inter";
            display: block;
            &::first-letter {
              text-transform: capitalize;
            }
          }
        }
      }
    }

    button.singButton {
      padding: 12px 21px;
      border: 1px solid black;
      background: none;
      border-radius: 6px;
      text-transform: capitalize;
      color: black;
      font-weight: 800;
      cursor: pointer;
      background: white;
      display: none;

      i.menuBar {
        span {
          width: 12px;
          height: 2px;
          display: block;
          background: black;
          margin-top: 2px;
          border-radius: 6px;
        }
      }
    }
  }
}

aside {
  max-width: 250px;
  width: 100%;
  display: block;
  position: fixed;
  right: 0;
  z-index: 998;
  background: rgba(255, 255, 255, 0.91);
  top: 0;
  box-shadow: -7px 0px 12px -3px #cecece5a;

  .container {
    height: 100%;
    position: relative;
    width: 95%;
    margin: auto auto;
    display: flex;
    justify-content: center;
    flex-direction: column;

    ul {
      text-align: right;
      list-style: none;
      width: 100%;
      margin: 0;
      padding: 0;
      padding-right: 10px !important;
      // border:1px solid red;
      box-sizing: border-box;

      li {
        text-transform: capitalize;
        padding: 8px 0;
        font-size: 20px;

        //box-shadow: 0px 0px 12px 2px #cecece5a;
        a {
          text-decoration: none;
        }
      }
    }

    i.menuClose {
      padding: 8px;
      cursor: pointer;
      outline: 1px solid black;
      border-radius: 5px;
      // width: auto;
      height: auto;
      display: block;
      position: absolute;
      bottom: 8px;
      box-sizing: border-box;
      right: 8px;

      span {
        width: 20px;
        height: 3px;
        background: black;
        display: block;
        margin-top: 4px;
        border-radius: 3px;

        &:nth-of-type(1) {
          margin-top: 0;
        }
      }
    }
  }
}

@media screen and (max-width:900px) {
  header {
    
    .container {
      nav {
        display: none;
      }

      button.singButton {
        display: block;
      }
    }
  }
  aside{
    top:80px
  }
}
@media screen and (max-width:600px) {
  header {
    bottom: 0!important;
    top:inherit;
    position: fixed;
  }
  aside{
    top:0
  }
}
</style>
<style>
@media screen and (max-width:600px) {
  main section.products{
    padding-bottom: 80px;
  }
  
}
